<template>
  <header class="main-header">
    <a href="#" class="logo"><span class="logo-mini">TF</span><span class="logo-lg">TuFanInc</span></a>
    <nav class="navbar navbar-static-top">
      <a href="#" class="sidebar-toggle" data-toggle="offcanvas" role="button">
        <span class="sr-only">Toggle navigation</span>
        <span class="icon-bar"></span>
        <span class="icon-bar"></span>
        <span class="icon-bar"></span>
      </a>
      <div class="navbar-custom-menu">
        <ul class="nav navbar-nav">
          <li class="dropdown messages-menu">
            <a href="/" target="_blank" >{{$t("layout.home")}}</a>
          </li>
          <li class="dropdown messages-menu">
            <a href="javascript:void(0);" @click="changeI18n" class="delete-one">{{$t('layout.i18n')}}</a>
          </li>
          <li class="dropdown user user-menu">
            <a href="javascript:void(0);" class="dropdown-toggle" data-toggle="dropdown">
              <img :src="avatar" class="user-image">
              <span class="hidden-xs">{{username}}</span>
            </a>
            <ul class="dropdown-menu">
              <li class="user-header">
                <img :src="avatar" class="img-circle">
                <p>{{username}}<small>{{permission}}</small></p>
              </li>
              <li class="user-footer">
                <div class="pull-left">
                  <router-link class="btn btn-default btn-flat" :to="mkRoute()">
                    {{$t('layout.setting')}}</router-link></div>
                <div class="pull-right">
                  <a class="btn btn-default btn-flat" href="javascript:void(0)" @click="logout">
                    {{$t('layout.logout')}}</a>
                  </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </nav>
  </header>
</template>

<script>
    export default {
      name: "layoutHead",
      data(){
        return{
          avatar:this.$cookies.get('avatar') || require('@static/uploads/avatar/header.jpg')
        }
      },
      methods:{
        changeI18n(){
          let lang = localStorage.getItem('i18n')
          lang = lang === 'zh'?'en':'zh'
          localStorage.setItem('i18n', lang)
          this.$i18n.locale = lang
        },
        logout(){
          this.$store.dispatch('user/logout').then( resp =>{
            if (resp){
              localStorage.clear()
              this.$cookies.keys().forEach((vol) =>{
                this.$cookies.remove(vol)
              })
              this.$router.push({
                path: '/login'
              })
            }
          })
        },
        mkRoute(){
          return {
            name: 'user_create_edit',
            query: {id: this.$cookies.get('uid')}
          }
        }
      },
      computed:{
        username(){
          return this.$cookies.get('username')
        },
        permission(){
          return this.$t('permission.super')
        }
      }
    }
</script>



