<template>
  <div class="wrapper">
    <layout-head/>
    <layout-sidebar/>
    <router-view/>
    <layout-footer/>
  </div>
</template>

<script>
    import '@static/global/bootstrap/bootstrap.min'
    import '@static/system/slimScroll/jquery.slimscroll.min'
    import '@static/system/dist/js/app.min.js'
    import {LayoutHead,LayoutSidebar,LayoutFooter} from "./components"

    export default {
      name: "index",
      components: {LayoutFooter, LayoutSidebar, LayoutHead },
      beforeCreate:function () {
        try{
          const classList = ['skin-blue','fixed','sidebar-mini']
          document.body.classList.add(...classList)
        }catch (e) {}

        // document.body.className = 'skin-blue fixed sidebar-mini'
      },
      beforeDestroy:function () {
        try {
          document.body.classList.remove('skin-blue fixed sidebar-mini')
        }catch (e) {}
      }
    }
</script>
<style>
  @import '../../../static/system/dist/css/skins/_all-skins.min.css';
</style>



