<template>
  <aside class="main-sidebar">
    <section class="sidebar">
      <!--用户面板-->
      <div class="user-panel">
        <div class="pull-left image">
          <img v-lazy="avatar" class="img-circle"></div>
        <div class="pull-left info">
          <p>{{username}}</p>
          <a href="#"><i class="fa fa-circle text-success"></i>{{status}}</a>
        </div>
      </div>
      <!--搜索面板-->
      <form action="#" method="get" class="sidebar-form">
        <div class="input-group">
          <input type="text" class="form-control" :placeholder="search">
          <span class="input-group-btn">
            <button type="submit" name="search"
                    class="btn btn-flat"><i class="fa fa-search"></i></button>
          </span>
        </div>
      </form>
      <!--菜单面板-->
      <ul class="sidebar-menu">
        <li class="header">{{menu}}</li>
        <tree-menu v-for="(model, index) in routes"
                   v-if="model.isMenu"
                   :model="model"
                   :key="index"/>
      </ul>
    </section>
  </aside>
</template>

<script>
    import {routes} from '@/router/index'
    import TreeMenu from "@/components/widgets/TreeMenu"

    export default {
      name: "layout_sidebar",
      components: {TreeMenu},
      data(){
        return {
          avatar: this.$cookies.get('avatar') || require('@static/uploads/avatar/header.jpg'),
          username:this.$cookies.get('username'),
          status:this.$t('status.online'),
          search:this.$t('layout.search'),
          menu:this.$t('layout.menu'),
          routes
        }
      }
    }
</script>



